
h2 {
	font-family: 'Indie Flower', cursive;
	text-align: center;
}
.invocations {
	overflow-x: hidden;
	overflow-y: scroll; /* has to be scroll, not auto */
	-webkit-overflow-scrolling: touch;
	height: calc(100vh - 40px);
}
*::-webkit-scrollbar {
	width: 0px;
	height: 0px;
}
*::-webkit-scrollbar-track {
	background: #FFF;
}
*::-webkit-scrollbar-thumb {
	background: #7B0707;
}
.ul-container {
	opacity: 0;
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
	transition: all .5s ease-out;
}
.ul-container.isLoad {
	opacity: 1;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}
.ul-container .ul-list,
.ul-container .ul-list .ul-list-item{
	display: block;
	width: 100%;
	text-align: center;
}
.ul-container .ul-list .ul-list-item {
	position: relative;
}
.ul-container .ul-list .ul-list-item .main-content{
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.ul-container .ul-list .ul-list-item .main-content.open-options{
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	padding-right: 10px;
	border-right: 1px solid #ddd;
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.share-content {
	position: absolute;
	top: 0;
	left: 50%;
	right: 0;
	bottom: 0;
	height: 30px;
	display: none;
	margin: auto;
	opacity: 0;
	-webkit-transform: translateX(50%);
	        transform: translateX(50%);
	transition: all .3s ease-out;
}
.share-content.open-options {
	display: block;
	opacity: 1;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	transition: all .3s ease-out;
}
.fr-doua {
	color: #aaa;
	font-style: italic;
	margin-top: 25px;
	margin-bottom: 10px;
}
.ph-doua {
	color: #37473F;
	font-weight: bold;
	margin-bottom: 25px;
}
.ul-list-item hr {
	border-top: 1px solid #8c8b8b;
	width: 80%;
	text-align: center;
}
.ul-list-item hr:after {
	content: '❃';
	display: inline-block;
	position: relative;
	top: -14px;
	padding: 0 10px;
	background-color: #FFF;
	color: #8c8b8b;
	font-size: 18px;
}



